.blog-header { $c: &;
	padding: 50px 0 100px 0;

	@media only screen and (max-width: $desktop) {
		padding: 20px 0;
	}

	@media only screen and (max-width: $tablet) {
		 padding: 10px 0 80px;
	}

	@media only screen and (max-width: $mobile) {
		padding: 0px 0 60px;
	}

	&__inner {
		display: flex;
		align-items: center;

		@media only screen and (max-width: $tablet) {
			flex-direction: column;
		}
	}

	&__left,
	&__right {
		width: 50%;

		@media only screen and (max-width: $tablet) {
			width: 100%;
		}
	}

	&__left {
		max-width: 590px;
		padding-right: 20px;
		margin-right: auto;

		@media only screen and (max-width: $tablet) {
			max-width: 100%;
			padding-right: 0;
		}
	}

	&__right {
		position: relative;

		@media only screen and (max-width: 1130px) {
			#{$c}__image {
				width: 400px;
				height: 420px;
			}
		}

		@media only screen and (max-width: $desktop) {
			#{$c}__image {
				width: 340px;
				height: 360px;
			}
		}

		@media only screen and (max-width: $tablet) {
			order: -1;

			#{$c}__image {
				width: 100%;
				height: 380px;
				margin-bottom: 40px;

				&::before {
					border-radius: 30px;
				}

				img {
					border-radius: 30px;
				}
			}
		}

		@media only screen and (max-width: $mobile) {
			#{$c}__image {
				height: 280px;
				margin-bottom: 32px;

				&::before {
					border-radius: 16px;
				}

				img {
					border-radius: 16px;
					box-shadow: none;
				}
			}
		}
	}

	&__title {
		margin-bottom: 20px;
		font-size: 58px;
		color: $heading-font-color;

		@media only screen and (max-width: 1130px) {
			font-size: 44px;
		}

		@media only screen and (max-width: $desktop) {
			font-size: 38px;
			margin-bottom: 24px;
		}

		@media only screen and (max-width: $mobile) {
			font-size: 32px;
		}
	}

	&__description {
		p {
			margin-bottom: 0;
			font-size: 18px;
			line-height: 1.6;
			color: $text-color;

			@media only screen and (max-width: $desktop) {
				font-size: 16px;
			}
		}
	}

	&__btn {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 40px;
		padding: 12px 20px;

		i {
			margin-right: 10px;
			font-size: 30px;
		}

		.cta-button {
			margin-right: 8px;
		}

		@media only screen and (max-width: $desktop) {
			margin-top: 32px;
		}

		@media only screen and (max-width: $mobile) {
			.cta-button,
			.works-button {
				font-size: 15px;
				padding: 10px;
			}
			padding: 0;
		}
	}

	&__image {
		position: relative;
		z-index: 10;
		width: 480px;
		height: 520px;
		margin-left: auto;

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			border-radius: 60px;
			background: $blue-gray;
		}

		img {
			position: absolute;
			top: 0;
			z-index: 1;
			height: 100%;
			width: 100%;
			border-radius: 60px;
			object-fit: cover;
			user-select: none;
			box-shadow: 0px 100px 80px rgba($primary-color, 0.07), 0px 41.7776px 33.4221px rgba($primary-color, 0.0503198), 0px 22.3363px 17.869px rgba($primary-color, 0.0417275), 0px 12.5216px 10.0172px rgba($primary-color, 0.035), 0px 6.6501px 5.32008px rgba($primary-color, 0.0282725), 0px 2.76726px 2.21381px rgba($primary-color, 0.0196802);
			transition: inherit;
			background: $blue-gray;
		}
	}
}
